* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 30px auto;
  padding: 15px;
}

.clearfix {
  clear: both;
  content: "";
  display: table;
}

h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 30px;
}

.profileContainer {
  float: left;
  width: 25%;
  padding: 15px;
}

.repoListContainer {
  float: right;
  width: 75%;
  padding: 15px;
}

.userAvatar {
  width: 120px;
  height: 120px;
  object-fit: contain;
  border-radius: 50%;
  max-width: 100%;
}

.userName {
  font-weight: 300;
  font-size: 20px;
  color: #6c757f;
}

ul {
  padding: 5px;
  list-style: none;
}

li {
  padding: 30px 0px;
  border-bottom: 1px solid #ddd;
}

a {
  text-decoration: none;
  font-weight: 500;
  font-size: 20px;
  display: block;
  margin-bottom: 15px;
}

a:hover {
  text-decoration: underline;
}

.repoLanguage {
  font-size: 14px;
  color: #6c757f;
  font-weight: 500;
  border-left: 4px #13ade1 solid;
  padding: 0px 15px 0px 5px;
}

.repoUpdated {
  font-size: 14px;
  color: #6c757f;
}

input {
  padding: 8px;
  border: none;
  outline: none;

  border: 1px solid #ddd;
  border-radius: 6px;
  display: block;
  width: 100%;
}

input:active,
input:focus {
  border-width: 2px;
  border-color: #13ade1;
  margin-bottom: -2px;
}
